.main-header-menu-toggle {
  z-index: 30;
  width: 24px;
  height: 24px;
  background-color: inherit;
  display: none;
  position: relative;
}

@media screen and (max-width: 1000px) {
  .main-header-menu-toggle {
    display: inline-block;
  }
}

.main-header-menu-toggle__item {
  width: 100%;
  height: 2px;
  opacity: .6;
  background: #fff;
  transition: all .3s;
  position: absolute;
  top: 9px;
  left: 0;
}

.main-header-menu-toggle:before, .main-header-menu-toggle:after {
  content: "";
  width: 100%;
  height: 2px;
  opacity: .6;
  background: #fff;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.main-header-menu-toggle:before {
  top: 0;
}

.main-header-menu-toggle:after {
  bottom: 0;
}

.main-header-menu-toggle.active:before {
  top: 9px;
  transform: rotate(45deg);
}

.main-header-menu-toggle.active:after {
  bottom: 9px;
  transform: rotate(-45deg);
}

.main-header-menu-toggle.active span {
  transform: scale(0);
}

